
.yy-child-header{
	height: 44px;
    padding: 10px;
    box-sizing: border-box;
    position: relative;
    line-height: 24px;
    background-color: #434343;
    color: #fff;
}
.yy-child-header span{
	position: absolute;
    left: 10px;
}
.yy-child-header h6{
	text-align: center;
}




