
.yy-home,.yy-artist,.yy-order,.yy-me{
    position: absolute;
    width: 100%;
    bottom: 0px;
    top: 0;
}
.yy-home{
	background-color: #f8f8f8;
}
.yy-tabs{
	width: 100%;
    height: 49px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: #ffffff;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
}
.yy-tab{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: center;
}
.yy-tab span{
	line-height: 34px;
}
.yy-title{
	font-size: 12px;
    -webkit-transform: scale(0.88888);
            transform: scale(0.88888);
    color: #cacaca;
}
.selected{
	color: orange;
}
.iconfont.selected{
	color: orange;
}




