
.yy-home-wraper{
	overflow: auto;
	width: 100%;
	height: 100%;
}
.yy-home-header{
	width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
    background: #ffffff;
    box-sizing: border-box;
}
.yy-home-search{
	width: 76%;
    position: relative;
}
.iconfont.icon-fangdajing{
	position: absolute;
    left: 4px;
    top: 7px;
    font-size: 18px;
    color: #aeaeae;
}
.yy-home-search-input{
	width: 100%;
    height: 24px;
    /* border-color: #dfdfdf; */
    /* border-radius: 5px; */
    text-indent: 24px;
    outline: none;
}
.yy-home-go-address{
	width: 18%;
    height: 30px;
    line-height: 30px;
    background: orange;
    text-align: center;
    border-radius: 5%;
    margin-left: 15px;
}
.yy-home-service{
	width: 100%;
	height: 180px;
}
.yy-home-service-item{
	float: left;
	width: 15%;
}
.yy-home-service-img{
	width: 100%;
}
.yy-home-service-img img{
	width: 100%;
}




